imonial-left .btn.testimonial-btn{margin-top:15px;padding:12px 40px;border:2px solid #ff4a57;background:transparent;border-radius:50px;-webkit-border-radius:50px;-moz-border-radius:50px;-ms-border-radius:50px;-o-border-radius:50px;transition:.4s;-webkit-transition:.4s;-moz-transition:.4s;-ms-transition:.4s;-o-transition:.4s}#testimonial .testimonial-left .btn.testimonial-btn:hover{background:#ff4a57}#testimonial .testimonial-right{font-style:italic;padding:40px 60px;background:#23263a}#testimonial .testimonial-right p{font-size:18px;color:#dfdfdf}#testimonial ul li{color:#ff4a57}#testimonial .quote-icon{position:absolute;top:6%;left:4%;font-size:28px}#testimonial .testimonial-position .testimonial-img img{border:2px solid #dfdfdf;height:75px;width:75px;border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%}#testimonial .testimonial-position .position{color:#dfdfdf}#testimonial .testimonial-position button{margin:0 5px;border:2px solid #dfdfdf;color:#dfdfdf;outline:0;background:none;width:50px;height:50px;border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%}#testimonial .carousel-control-next svg{opacity:.7;position:absolute;bottom:10%;right:0;border:2px solid #dfdfdf;height:40px;width:40px;background:transparent;border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%;transition:.2s;-webkit-transition:.2s;-moz-transition:.2s;-ms-transition:.2s;-o-transition:.2s}#testimonial .carousel-control-next svg:hover{border-color:#ff4a57}
